<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link to="/" class="breadcrumbs__link">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{name: 'cart'}" class="breadcrumbs__link">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{name: 'order'}" class="breadcrumbs__link">
             
          </router-link>
        </li>
      </ul>

      <h1 class="content__title">
        
      </h1>
      <span class="content__info">
        {{ cartProductCount }} ()
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText title="" placeholder="   " v-model="formData.name" :error="formError.name"/>
            <BaseFormText title=" " placeholder="  " v-model="formData.address" :error="formError.address"/>
            <BaseFormText title="" type="tel" placeholder="  " v-model="formData.phone" :error="formError.phone"/>
            <BaseFormText title="Email" type="email" placeholder="  Email" v-model="formData.email" :error="formError.email"/>
            <BaseFormTextArea title="  " placeholder=" " v-model="formData.comment" :error="formError.comment"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                     <b></b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                     <b>500 </b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                      
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                      
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block" v-if="productLoading"> ...</div>
        <div class="cart__block" v-else-if="products.length == 0"> </div>
        <div class="cart__block" v-else>
          <ul class="cart__orders">
            <li class="cart__order" v-for="prod in products" :key="prod.productId">
              <h3>{{ prod.product.title }}</h3>
              <b>{{ prod.product.price  * prod.amount | numberFormat }} </b>
              <span>: {{ prod.productId }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>: <b>500 </b></p>
            <p>: <b>{{ cartProductCount }}</b> (-)   <b>{{ totalPrice | numberFormat }} </b></p>
          </div>

          <div class="loader" v-if="formSending"> </div>
          <button class="cart__button button button--primery" type="submit" v-else>
             
          </button>

        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>  !</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>

</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextArea from '@/components/BaseFormTextArea.vue';
import { mapGetters } from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  components: { BaseFormText, BaseFormTextArea },
  name: 'OrderPage',
  filters: { numberFormat },
  computed: {
    ...mapGetters({
      productLoading: 'cartProductsLoading',
      products: 'cartDetailsProducts',
      totalPrice: 'cartTotalPrice',
      cartProductCount: 'cartProductCount',
    }),
  },

  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      formSending: false,
    };
  },
  methods: {
    order() {
      this.formSending = true;
      this.formError = {};
      this.formErrorMessage = '';
      return new Promise((resolve) => { setTimeout(resolve, 2000); })
        .then(() => {
          axios.post(`${API_BASE_URL}/api/orders`, {
            ...this.formData,
          }, {
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            },
          })
            .then((response) => {
              this.formData = {};
              this.$store.commit('resetCart');
              this.$store.commit('updateOrderInfo', response.data);
              this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
            })
            .catch((error) => {
              this.formError = error.response.data.error.request || {};
              this.formErrorError = error.response.data.error.message || '';
            })
            .finally(() => { this.formSending = false; });
        });
    },
  },
};
</script>

