<script>

// import products from '@/data/products';
// import categories from '@/data/categories';
import goToPage from '@/helpers/goToPage';
import numberFormat from '@/helpers/numberFormat';
import ProductItemCounter from '@/components/ProductItemCounter.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { mapActions } from 'vuex';

export default {
  components: { ProductItemCounter },
  data() {
    return {
      productAmount: 1,
      productData: null,
      productLoading: false,
      productsLoadingFailing: false,
      productAdded: false,
      productAddSending: false,
    };
  },
  filters: {
    numberFormat,
  },
  computed: {
    product() {
      // return products.find((p) => p.id === +this.$route.params.id);
      return this.productData ? {
        ...this.productData,
        img: this.productData.image.file.url,
      } : {};
    },
    category() {
      // return categories.find((c) => c.id === this.product.categoryId);
      return this.productData.category;
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),
    goToPage,
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      this.addProductToCart({
        productId: this.product.id,
        amount: this.productAmount,
      }).then(() => {
        this.productAdded = true;
        this.productAddSending = false;
      });
    },
    loadProduct() {
      this.productLoading = true;
      this.productsLoadingFailing = false;
      axios.get(`${API_BASE_URL}/api/products/${this.$route.params.id}`)
        .then((response) => { this.productData = response.data; })
        .catch(() => { this.productsLoadingFailing = true; })
        .then(() => { this.productLoading = false; });
    },
  },
  created() {
    this.loadProduct();
  },
  watch: {
    '$route.params.id': function () {
      this.loadProduct();
    },
    /* '$route.params.id': {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    }, */
  },
};
</script>

